.fl{
  float:left;
}
.fr{
  float:right;
}

.ajml-container h3{
    padding-bottom: 20px;

}

.ajml-container h4{
    padding-top: 20px;
    padding-bottom: 10px;
}


.ajml-banner{
    padding-top: 80px;
    width:1000px;
    margin:0 auto;
}

.ajml-overview{
    font-size: 16px;
    line-height: 1.5;
    padding-bottom: 20px;
    padding-top: 20px;
}

.ajml-speakers{
    padding-bottom: 50px;

}




.ajml-program{
    padding-bottom: 50px;
}

.ajml-table{
    width: 100%;
    line-height: 1.5;
}

.ajml-table td{
    padding: 12px;
}

.ajml-table tbody tr td:first-child {
  width: 25%;
}

.ajml-container{
    width:1000px;
    margin:0 auto;
    padding-bottom: 100px;
}

.ajml-head{
    width:100%;
    top:0px;
    position:fixed;
    background-color: rgba(0,0,0,0.9);
    background-size:100% 100%;
    z-index:9999;
}

.ajml-head-inner{
    width:1000px;
    height:80px;
    margin:0 auto;
}


.ajml-head-nav ul li{
    margin:33px 0 0 56px;
    float: left;
    line-height:16px;
    list-style: none;
}


.ajml-head-nav ul li a{
    color:#fff;
    font-size:20px;
    font-weight:700;
}

.ajml-head-nav ul li a:hover{
    color:#007bff;
    text-decoration: underline;
}

.ajml-logo{
    padding-top: 15px;
}

.ajml-logo a{
    font-size: 32px;
    font-weight: 600;
    color:#fff;
}

@media (max-width: 575.98px) {
    .ajml-container{
	width:100%;
	padding-left: 20px;
	padding-right: 20px;
    }

    .ajml-head-inner{
	width:100%;
	
    }

    .ajml-head-inner{
	padding-left: 20px;
	padding-right: 20px;
    }
}
